.cardContainer{
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: sticky;
    top: 0px;
    .card{
        display: flex;
        flex-direction: column;
        position: relative;
        top: -25%;
        height: 500px;
        width: 1000px;
        border-radius: 25px;
        padding:50px;
        transform-origin: top;
        h2{
            text-align: center;
            margin: 0px;
            font-size: 28px;
        }
        .body{
            display: flex;
            height: 100%;
            margin-top: 50px;
            gap: 50px;

            .description{
                width: 40%;
                position: relative;
                top: 10%;
                p{
                    font-size: 16px;
                    &::first-letter{
                        font-size: 28px;
                        font-family: 'Title';
                    }
                }
                span{
                    display: flex;
                    align-items: center;
                    gap: 5px;
                    a{
                        font-size: 12px;
                        text-decoration: underline;
                        cursor: pointer;
                    }
                }
            }

            .imageContainer{
                position: relative;
                width: 60%;
                height: 80%;
                border-radius: 25px;
                overflow: hidden;
                .inner{
                    width: 100%;
                    height: 100%;
                    img{
                        object-fit: cover;
                        width: 100%;
                        height: 100%;
                    }
                }
            }
        }
    }
}

.mainCard{
    position: relative;
    margin-top: 50vh;
}
