.main{
    margin-top: 50vh;
    margin-bottom: 100vh;
}

.container{
    height: 300vh;
    position: relative;
    .sticky{
        position: sticky;
        overflow: hidden;
        top: 0;
        height: 100vh;

        .el{
            width: 100%;
            height: 100%;
            top: 0;
            position: absolute;
            display: flex;
            align-items: center;
            justify-content: center;
            .imageContainer{
                position: relative;
                /*width: 25vw;*/
                /*height: 25vh;*/
                img{
                    object-fit: cover;
                    width: 25vw;
                    height: 25vh;
                }
            }
            &:nth-of-type(2){
                .imageContainer{
                    top: -30vh;
                    left: 5vw;
                    width: 35vw;
                    height: 30vh;
                    img {
                        object-fit: cover;
                        width: 35vw;
                        height: 30vh;
                    }
                }
            }
            &:nth-of-type(3){
                .imageContainer{
                    top: -10vh;
                    left: -25vw;
                    width: 20vw;
                    height: 45vh;
                    img {
                        object-fit: cover;
                        width: 20vw;
                        height: 45vh;
                    }
                }
            }
            &:nth-of-type(4){
                .imageContainer{
                    left: 27.5vw;
                    width: 25vw;
                    height: 25vh;
                    img {
                        object-fit: cover;
                        width: 25vw;
                        height: 25vh;
                    }
                }
            }
            &:nth-of-type(5){
                .imageContainer{
                    top: 27.5vh;
                    left: 5vw;
                    width: 20vw;
                    height: 25vh;
                    img {
                        object-fit: cover;
                        width: 20vw;
                        height: 25vh;
                    }
                }
            }
            &:nth-of-type(6){
                .imageContainer{
                    top: 27.5vh;
                    left: -22.5vw;
                    width: 30vw;
                    height: 25vh;
                    img {
                        object-fit: cover;
                        width: 30vw;
                        height: 25vh;
                    }
                }
            }
            &:nth-of-type(7){
                .imageContainer{
                    top: 22.5vh;
                    left: 25vw;
                    width: 15vw;
                    height: 15vh;
                    img {
                        object-fit: cover;
                        width: 15vw;
                        height: 15vh;
                    }
                }
            }
        }
    }
}

